export async function _login(i){
	// BaaS.auth.anonymousLogin().then(user => {
	// 	console.log(user)
	// }).catch(err => {
	// 	// HError
	// })
	console.log(i);
	query.queryObject.$and = []
	var r2
	query.contains('phone', i.phone)
	r2 = await user_info
	.setQuery(query)
	.find()
	.then(
		(res) => {
			var r1
			// console.log(res)
			res.data.objects.forEach((e) => {
				if(i.password == e.password) r1=true
				else r1=false
			})
			if(res.data.objects.length<1||r1==false)
			uni.showToast({
				title: '账号密码错误',
				// 勾号消失
				icon: 'none',
			})
			return r1
		},
		(err) => {
			console.log('error')
		}
	)
	wx.auth.login({username: i.phone, password: i.password}).then(user => {
		console.log(user)
	}).catch(err=>{
		// HError
		console.log('bukey');
	})
	// wx.BaaS.auth.loginWithWechat().then(user => {
	//   // 登录成功
	// 	console.log(user);
	// }, err => {
	//   // 登录失败
	// 	console.log('denglushibai');
	// })
	return r2
}

export function updateuserInfo(recordID,key,value){
	let product = user_info.getWithoutData(recordID)
	product.set(key, value)
	product.update()
}
// 获取用户信息
export async function getUserInfo(id){
	let userInfo =[]
	userInfo = await user_info
		.get(id)
      .then(
        (res) => {
					userInfo = res.data
					return userInfo
        },
        (err) => {
          console.log('error')
        }
      )
			return userInfo
}

// 获取试卷列表
export async function getUserPaper(query){
  let paperList= []
  // query.queryObject.$and = []
    // query.contains('user_id', userId)
    paperList = await paper_user
      .setQuery(query)
      .limit(100)
      .find()
      .then(
        (res) => {
          paperList = res.data.objects
          return paperList
        },
        (err) => {
          console.log('error')
        }
      )
      return paperList
}

